.footer{
  padding: 80px 0 64px;
  &__nav{
    display: flex;
    justify-content: center;
  }
  &__btn{
    display: flex;
    align-items: center;
    svg{
      margin-left: 16px;
      width: 32px;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
        fill: $primary;
      }
    }
  }
  &__c{
    margin-top: 184px;
    text-align: center;
    opacity: 0.5;
  }
}


@media screen and (max-width: 768px){
  .footer{
    padding: 40px 0 24px;
    &__btn{
      font-size: 32px;
      line-height: 40px;
      svg{
        width: 16px;
        margin-left: 8px;
      }
    }
    &__c{
      margin-top: 80px;
    }
  }
}
