.display2{
  font-weight: 900;
  font-size: 80px;
  line-height: 110%;
}

.display3{
  font-weight: 700;
  font-size: 64px;
  line-height: 112%;
}

.text40{
  font-size: 40px;
  line-height: 140%;
}

.text24{
  font-size: 24px;
  line-height: 133%;
}

.text17{
  font-size: 17px;
  line-height: 141%;
}

.gray-text{
  color: $gray;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

@media screen and (max-width: 1344px){
  .display3{
    font-size: 56px;
    line-height: 114%;
  }
}


@media screen and (max-width: 1024px){
  .display2{
    font-size: 64px;
  }

  .display3{
    font-size: 48px;
    line-height: 117%;
  }

  .text40{
    font-size: 32px;
    line-height: 150%;
  }
}


@media screen and (max-width: 768px){
  .display2{
    font-size: 40px;
  }

  .display3{
    font-size: 32px;
    line-height: 125%;
  }

  .text40,
  .text24{
    font-size: 17px;
    line-height: 141%;
  }
}
