@import "../mixins";
.menu{
  position: fixed;
  top: 32px;
  right: calc((100% - 1344px) / 2);
  z-index: 20;
  padding: 0 60px;
  display: flex;
  align-items: center;
  &__dropdown{
    position: absolute;
    right: 60px;
    min-width: calc(100% - 120px);
    top: 100%;
    margin-top: 8px;
    padding: 32px;
    background: rgba(0, 0, 0, 0.07);
    backdrop-filter: blur(15px);
    border-radius: 32px;
    @include grid100gap(10px);
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    z-index: 11;
  }
  &__bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    display: none;
  }
  &__current,
  &__link{
    line-height: 24px;
  }
  &._open{
    .burger{
      background-image: url(../img/burger2.svg);
    }
    .menu__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .menu__bg{
      display: block;
    }
  }
}

.burger,
.menu__current{
  padding: 0 32px;
  display: flex;
  align-items: center;
  height: 48px;
  background: rgba(0, 0, 0, 0.07);
  backdrop-filter: blur(15px);
  border-radius: 100px;
}

.burger{
  background-image: url(../img/burger.svg);
  @include bgImgSize(24px);
  padding: 0;
  width: 48px;
  margin-left: 8px;
  flex-shrink: 0;
}

@media screen and (max-width: 1344px){
  .menu{
    right: 0;
    padding: 0 24px;
    &__dropdown{
      right: 24px;
      min-width: calc(100% - 48px);
    }
  }
}

@media screen and (max-width: 768px){
  .menu{
    top: 16px;
    padding: 0 16px;
    &__dropdown{
      right: 16px;
      padding: 16px;
      border-radius: 16px;
      min-width: calc(100% - 32px);
    }
    &__current{
      height: 40px;
      padding: 0 16px;
    }
    &__current,
    &__link{
      font-size: 14px;
      line-height: 19px;
    }
  }

  .burger{
    width: 40px;
    height: 40px;
    background-size: 20px auto;
  }
}
