@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    max-width: 1344px;
    margin: 0 auto;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 60px;

}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 1344px) {
   .container{
       padding: 0 24px;
   }
}


@media screen and (max-width: 768px) {
   .container{
       padding: 0 16px;
   }
}
