@import "../mixins";
.main,
.company,
.target,
.same,
.opportunity2{
  &__img{
    img{
      width: 100%;
      border-radius: 0 0 80px 80px;
    }
    ._view2{
      display: none;
    }
  }
}

.main{
  color: #fff;
  &__inner{
    position: relative;
  }
  &__text{
    position: absolute;
    top: 40px;
    left: 0;
    max-width: 600px;
  }
  &__logo{
    margin-bottom: 48px;
    display: block;
    width: 134.15px;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 124px;
    line-height: 103%;
  }
  &__desc{
    margin-top: 16px;
    max-width: 450px;
  }
}

.company{
  padding: 80px 0;
  margin-top: 80px;
  &__text{
    max-width: 808px;
    margin: 0 auto;
    text-align: center;
  }
  &__desc{
    margin-top: 24px;
  }
  &__stat{
    display: flex;
    justify-content: center;
    margin: 72px auto 0;
  }
  &__img{
    margin-top: 152px;
    position: relative;
    &-text{
      position: absolute;
      width: calc((100% - 120px) / 3);
      right: 60px;
      top: -32px;
      font-size: 24px;
      line-height: 133%;
    }
  }
}

.company-num{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 44px;
  &__title{
    padding-top: 32px;
    font-weight: 700;
    font-size: 120px;
    line-height: 100%;
    position: relative;
    &:before{
      content: '';
      z-index: -1;
      position: absolute;
      width: 97px;
      height: 97px;
      background-image: url(../img/arrow.webp);
      @include bgImgSize(100%);
      right: -16px;
      top: 0;
    }
  }
  &__desc{
    font-size: 24px;
    line-height: 133%;
  }
}

.target{
  padding: 80px 0;
  &__title{
    max-width: 75%;
    span{
      color: rgba(0, 0, 0, 0.3);
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: repeat(2,40.7%);
    justify-content: space-between;
    padding: 0 60px;
    position: absolute;
    width: 100%;
    left: 0;
    top: -120px;
  }
  &__text{
    @include grid100gap(32px);
    font-size: 24px;
    line-height: 133%;
    p{
      line-height: inherit;
    }
  }
  &__text2{
    max-width: 288px;
    opacity: .5;
  }
  &__img{
    position: relative;
    margin-top: 184px;
  }
}

.product{
  padding: 80px 0;
  &__top{
    text-align: center;
    max-width: calc(83.3% - 12px);
    margin: 0 auto;
  }
  &__desc{
    margin-top: 24px;
  }
  &__by{
    margin-top: 32px;
    text-align: left;
    @include center;
    .avatar{
      margin-right: 16px;
    }
  }
}

.reviews{
  margin-top: 336px;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 12px));
  gap: 80px 24px;
  .review-card{
    &:nth-child(odd){
      transform: translateY(-216px);
    }
  }
}

.opportunity{
  padding: 80px 0;
  &__top{
    position: relative;
  }
  &__title,
  &__wrap{
    width: calc(66.7% - 12px);
  }
  &__wrap{
    margin-top: 32px;
  }
  &__img{
    position: absolute;
    width: calc(33.3% - 12px);
    right: 0;
    top: 56px;
    &-wrap{
      position: relative;
      padding-bottom: 97%;
      img{
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}

.workers{
  margin-top: 136px;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 18px));
  gap: 24px;
  .worker-card{
    &:nth-child(odd){
      transform: translateY(-40px);
    }
  }
}

.same{
  padding: 80px 0;
  &__img{
    padding-top: 104px;
    position: relative;
    &-text{
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: calc(66.7% - 95px);
      text-align: center;
    }
    &-who{
      position: absolute;
      bottom: 16px;
      left: 50%;
      transform: translateX(-50%);
      padding: 0 12px;
      background: rgba(255, 255, 255, 0.7);
      border-radius: 100px;
      display: flex;
      align-items: center;
      height: 32px;
    }
  }
}

.opportunity2{
  padding: 80px 0;
  position: relative;
  &__top{
    max-width: calc(66.7% - 12px);
    @include grid100gap(32px);
  }
  &__items{
    margin-top: 96px;
    @include grid100gap(64px);
    padding-bottom: 600px;
  }
  &__img{
    position: absolute;
    bottom: 80px;
    left: 0;
    width: 100%;
    z-index: -1;
  }
}

.team{
  padding: 80px 0;
  &__top{
    display: grid;
    grid-template-columns: calc(58.33%) auto;
    align-items: center;
    gap: 24px;
  }
  &__desc{
    margin-top: 32px;
  }
  &__num{
    display: flex;
    justify-content: center;
    .company-num{
      align-items: center;
    }
  }
  &__img{
    margin-top: 80px;
    img{
      width: 100%;
    }
    ._view2{
      display: none;
    }
  }
}

.principle{
    padding: 80px 0;
    &__title{
      width: calc(91.67% - 12px);
      span{
        opacity: .3;
      }
    }
    &__desc{
      margin-top: 32px;
      width: calc(81.81% - 12px);
    }
  &__grid{
    margin-top: 112px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 12px));
    gap: 24px;
  }
}


@media screen and (max-width: 1344px){
  .main{
    &__title{
      font-size: 108px;
    }
    &__desc{
      max-width: 393px;
    }
  }

  .company{
    &__img{
      margin-top: 184px;
      &-text{
        width: calc((100% - 48px) / 2);
        padding-right: 16px;
        right: 24px;
        top: -64px;
      }
    }
  }

  .company-num{
    margin: 0 40px;
    &__title{
      font-size: 96px;
    }
  }

  .target{
    &__title{
      max-width: 730px;
    }
    &__wrap{
      grid-template-columns: 50% 33.33%;
      gap: 24px;
      padding: 0 24px;
      top: -160px;
    }
    &__text{
      max-width: 635px;
    }
    &__text2{
      padding-right: 24px;
      max-width: 100%;
    }
    &__img{
      margin-top: 224px;
    }
  }

  .same{
    &__img{
      &-text{
        width: calc(66.7% - 75px);
      }
    }
  }

  .team{
    &__top{
      grid-template-columns: 66.7% auto;
    }
    .company-num{
      &__title{
        padding-top: 32px;
        font-size: 96px;
        &:before{
          width: 97px;
          height: 97px;
          right: -16px;
          top: 0;
        }
      }
      &__desc{
        font-size: 24px;
        line-height: 133%;
      }
    }
  }

  .principle{
    &__desc{
      br{
        display: none;
      }
    }
    &__title,
    &__desc{
      width: calc(83.33% - 12px);
    }
  }
}

@media screen and (max-width: 1024px){
  .main,
  .company,
  .target,
  .same,
  .opportunity2{
    &__img{
      img{
        border-radius: 0 0 48px 48px;
      }
    }
  }

  .main{
    &__text{
      max-width: 42%;
    }
    &__title{
      font-size: 72px;
      line-height: 111%;
    }
    &__desc{
      margin-top: 8px;
    }
  }

  .company{
    &__text{
      max-width: 66.66%;
    }
    &__desc{
      font-size: 32px;
      line-height: 150%;
      margin-top: 16px;
    }
    &__stat{
      margin-top: 64px;
      max-width: 480px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px 10px;
    }
    &__img{
      margin-top: 168px;
      &-text{
        padding-right: 0;
        right: 50%;
        transform: translateX(50%);
        font-size: 17px;
        line-height: 141%;
        top: unset;
        bottom: 100%;
        margin-bottom: 16px;
      }
    }
  }

  .company-num{
    margin: 0;
    &:last-child{
      grid-column: 1/-1;
    }
    &__desc{
      font-size: 17px;
      line-height: 141%;
    }
  }

  .target{
    &__wrap{
      top: -120px;
      grid-template-columns: 50% 41.66%;
    }
    &__img{
      margin-top: 168px;
    }
    &__text{
      font-size: 17px;
      line-height: 141%;
      gap: 24px;
    }
  }

  .product{
    &__top{
      max-width: 720px;
    }
    &__desc{
      margin-top: 16px;
    }
  }

  .opportunity{
    &__img{
      top: unset;
      bottom: 13px;
    }
    &__title{
      width: calc(75% - 12px);
    }
    &__wrap{
      max-width: calc(41.67% - 12px);
      margin-top: 24px;
    }
    &__desc{
      font-size: 17px;
      line-height: 141%;
    }
  }

  .workers{
    grid-template-columns: repeat(3,calc(100% / 3 - 16px));
    .worker-card:nth-child(3)~.worker-card{
      display: none;
    }
  }

  .same{
    &__img{
      padding-top: 120px;
      &-text{
        width: calc(84% - 24px);
      }
    }
  }

  .opportunity2{
    &__top{
      max-width: calc(83.33% - 12px);
      gap: 24px;
    }
    &__desc{
      font-size: 17px;
      line-height: 24px;
    }
    &__items{
      margin-top: 56px;
      padding-bottom: 448px;
    }
  }

  .team{
    &__desc{
      margin-top: 24px;
      font-size: 17px;
      line-height: 24px;
      max-width: 410px;
    }
    &__img{
      margin-top: 64px;
    }
  }

  .principle{
    &__title,
    &__desc{
      width: calc(91.67% - 12px);
    }
    &__desc{
      margin-top: 24px;
      font-size: 17px;
      line-height: 141%;
    }
    &__grid{
      margin-top: 80px;
    }
  }
}


@media screen and (max-width: 768px){
  .main,
  .company,
  .target,
  .same,
  .opportunity2{
    &__img{
      img{
        border-radius: 0 0 24px 24px;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .main{
    color: $black;
    &__text{
      top: 26px;
      max-width: 100%;
      width: 100%;
    }
    &__logo{
      margin-bottom: 42px;
      width: 83.85px;
      ._mob-black{
        fill: $black;
      }
    }
    &__title{
      font-size: 48px;
      line-height: 100%;
    }
    &__desc{
      max-width: 66.6%;
    }
  }

  .company{
    padding: 40px 0;
    margin-top: 40px;
    &__text{
      text-align: left;
      max-width: 86.3%;
      margin-left: 0;
    }
    &__desc{
      font-size: 17px;
      line-height: 141%;
    }
    &__stat{
      max-width: 265px;
      margin: 40px 0 0;
      gap: 10px;
    }
    &__img{
      margin-top: 146px;
      &-text{
        width: 100%;
        padding: 0 16px;
        margin-bottom: 8px;
      }
    }
  }

  .company-num{
    &__title{
      font-size: 56px;
      padding-top: 19.2px;
      &:before{
        width: 58.2px;
        height: 58.2px;
        right: -9.6px;
      }
    }
  }

  .target{
    padding: 40px 0;
    &__wrap{
      z-index: 2;
      position: relative;
      top: unset;
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 16px;
      padding: 0 16px;
    }
    &__img{
      margin-top: 0;
      padding-bottom: 312px;
      img{
        position: absolute;
        z-index: -1;
        bottom: 0;
        left: 0;
      }
    }
  }

  .product{
    padding: 40px 0;
    &__by{
      margin-top: 24px;
      .avatar{
        width: 56px;
        height: 56px;
      }
    }
  }

  .reviews{
    grid-template-columns: 100%;
    gap: 56px;
    margin-top: 48px;
    .review-card{
      &:nth-child(odd){
        transform: unset;
      }
    }
  }

  .opportunity{
    padding: 40px 0;
    &__title{
      width: 100%;
    }
    &__wrap{
      width: 100%;
      max-width: 100%;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
      margin-top: 16px;
    }
    &__img{
      position: static;
      width: 100%;
      &-wrap{
        height: 188px;
        padding: 0;
      }
    }
    &__desc{
      width: calc(100% + 40px);
    }
  }

  .workers{
    margin-top: 52px;
    grid-template-columns: repeat(2,calc(50% - 8px));
    gap: 16px;
    .worker-card{
      &:nth-child(3)~.worker-card{
        display: block;
      }
    }
  }

  .same{
    padding: 40px 0;
    &__img{
      padding: 0;
      &-text{
        position: static;
        transform: unset;
        width: 100%;
        padding: 0 32px;
      }
    }
  }

  .opportunity2{
    padding: 40px 0;
    &__top{
      max-width: 100%;
      padding-right: 24px;
      gap: 16px;
    }
    &__items{
      margin-top: 40px;
      gap: 40px;
      padding-bottom: 284px;
    }
  }

  .team{
    padding: 40px 0;
    &__top{
      grid-template-columns: 100%;
      gap: 32px;
    }
    &__desc{
      margin-top: 16px;
    }
    &__img{
      margin-top: 32px;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .principle{
    padding: 40px 0;
    &__title,
    &__desc{
      width: 100%;
      padding-right: 16px;
    }
    &__desc{
      margin-top: 16px;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 40px;
    }
  }
}


@media screen and (max-width: 400px){
  .company{
    &__img{
      margin-top: 192px;
    }
  }
}
