@import "../mixins";
.review-card{
  display: block;
  &:hover{
    color: $black;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 64px;
    }
  }
  &__content{
    padding: 24px 32px 0;
    max-width: 520px;
  }
  &__by{
    display: flex;
    align-items: center;
    .avatar{
      width: 48px;
      height: 48px;
      margin-right: 16px;
    }
  }
  &__text,
  &__title{
    margin-top: 16px;
  }
}

.worker-card{
  position: relative;
  &__img{
    @include imgCover(138.8%);
    display: block;
    img{
      border-radius: 40px;
    }
  }
  &__content{
    text-align: center;
    position: absolute;
    bottom: 16px;
    left: 0;
    width: 100%;
    @include grid100gap(8px);
    color: #fff;
  }
}

.opportunity-card{
  display: grid;
  grid-template-columns: calc(16.6% - 8px) calc(75% - 8px);
}

.principle-card{
  @include center;
  background: linear-gradient(135deg, #F2F2F2 17.19%, #FFF9EB 100%);
  border-radius: 48px;
  position: relative;
  &__img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
  &__title{
    position: absolute;
    padding: 40px;
    left: 0;
    top: 0;
    max-width: 504px;
  }
  @media screen and (min-width: 769px){
    &._big{
      grid-column: 1/-1;
      display: grid;
      grid-template-columns: calc(41.7% - 12px) calc(58.3% - 12px);
      gap: 24px;
      align-items: flex-start;
      .principle-card__title{
        position: static;
      }
    }
  }
}


@media screen and (max-width: 1344px){
  .opportunity-card{
    display: grid;
    grid-template-columns: calc(8.33% - 8px) calc(83.3% - 8px);
  }
}


@media screen and (max-width: 1024px){
  .review-card{
    &__img{
      img{
        border-radius: 40px;
      }
    }
    &__content{
      padding: 16px 24px 0;
    }
    .avatar{
      width: 40px;
      height: 40px;
    }
    .text24{
      font-size: 17px;
      line-height: 141%;
    }
  }

  .worker-card {
    &__img {
      img {
        border-radius: 32px;
      }
    }
  }

  .principle-card{
    border-radius: 32px;
    &__title{
      padding: 32px;
      font-size: 17px;
      line-height: 141%;
      max-width: 412px;
    }
  }
}


@media screen and (max-width: 768px){
  .review-card {
    &__img {
      img {
        border-radius: 24px;
      }
    }
  }

  .worker-card{
    &__img{
      img{
        border-radius: 16px;
      }
    }
    &__content{
      gap: 0;
      bottom: 8px;
    }
    .text17{
      font-size: 12px;
      line-height: 133%;
    }
  }

  .opportunity-card{
    display: grid;
    grid-template-columns: 32px calc(100% - 32px);
  }

  .principle-card{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    &__title{
      padding: 24px;
    }
  }
}

@media screen and (max-width: 360px){
  .principle-card{
    padding-top: 32px;
  }
}
